___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (max-width: 768px) {

  .about {
    padding: 0px;
  }

  .about-neangancafe {
    padding: 20px;
    flex-direction: column;
  }

  .figure-neangancafe {
    margin-right: 0px;
    margin-bottom: 1.5rem;
  }

  .content-neangancafe .title-neangancafe {
    font-size: 22px;
  }

  .content-neangancafe .description-neangancafe {
    font-size: 12px;
  }

  .content-neangancafe .button-neangancafe {
    font-size: 13px;
  }
}

@media screen and (min-width: 650px) {
  .app-bar {
    grid-template-columns: 1fr auto;
    padding: 8px 32px;
  }

  .app-bar .bar_brand h1 {
    font-size: 1.5em;
  }

  .app-bar .bar_menu {
    display: none;
  }

  .app-bar .bar_navigation {
    position: static;
    width: 100%;
  }

  .app-bar .bar_navigation ul li {
    display: inline-block;
  }

  .app-bar .bar_navigation ul li a {
    display: inline-block;
    width: 120px;
    text-align: center;
    margin: 0;
  }

  .cafes {
    grid-template-columns: 1fr 1fr;
  }

  .cafe {
    grid-template-columns: auto 1fr;
  }

  .cafe .cafe__title {
    grid-column-start: 1;
    grid-column-end: 3;
  }

  .cafe .cafe__overview {
    grid-column-start: 1;
    grid-column-end: 3;
  }
}

@media screen and (max-width: 380px) {
  .jumbotron h1 {
    font-size: 1.5rem;
    padding: 270px 0 270px;
  }

  .cafe {
    max-width: 320px;
    margin: 0 auto;
    margin-top: 1rem;
    margin-bottom: 2rem;
  }
}

@media screen and (min-width: 800px) {
  .app-bar .bar_brand h1 {
    font-size: 2em;
  }
}

@media screen and (min-width: 850px) {
  .cafes {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (min-width: 1200px) {
  .cafes {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media screen and (min-width: 1600px) {
  .cafes {
    grid-template-columns: repeat(5, 1fr);
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/responsive.css"],"names":[],"mappings":"AAAA;;EAEE;IACE,YAAY;EACd;;EAEA;IACE,aAAa;IACb,sBAAsB;EACxB;;EAEA;IACE,iBAAiB;IACjB,qBAAqB;EACvB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,eAAe;EACjB;AACF;;AAEA;EACE;IACE,+BAA+B;IAC/B,iBAAiB;EACnB;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,gBAAgB;IAChB,WAAW;EACb;;EAEA;IACE,qBAAqB;EACvB;;EAEA;IACE,qBAAqB;IACrB,YAAY;IACZ,kBAAkB;IAClB,SAAS;EACX;;EAEA;IACE,8BAA8B;EAChC;;EAEA;IACE,+BAA+B;EACjC;;EAEA;IACE,oBAAoB;IACpB,kBAAkB;EACpB;;EAEA;IACE,oBAAoB;IACpB,kBAAkB;EACpB;AACF;;AAEA;EACE;IACE,iBAAiB;IACjB,sBAAsB;EACxB;;EAEA;IACE,gBAAgB;IAChB,cAAc;IACd,gBAAgB;IAChB,mBAAmB;EACrB;AACF;;AAEA;EACE;IACE,cAAc;EAChB;AACF;;AAEA;EACE;IACE,qCAAqC;EACvC;AACF;;AAEA;EACE;IACE,qCAAqC;EACvC;AACF;;AAEA;EACE;IACE,qCAAqC;EACvC;AACF","sourcesContent":["@media screen and (max-width: 768px) {\r\n\r\n  .about {\r\n    padding: 0px;\r\n  }\r\n\r\n  .about-neangancafe {\r\n    padding: 20px;\r\n    flex-direction: column;\r\n  }\r\n\r\n  .figure-neangancafe {\r\n    margin-right: 0px;\r\n    margin-bottom: 1.5rem;\r\n  }\r\n\r\n  .content-neangancafe .title-neangancafe {\r\n    font-size: 22px;\r\n  }\r\n\r\n  .content-neangancafe .description-neangancafe {\r\n    font-size: 12px;\r\n  }\r\n\r\n  .content-neangancafe .button-neangancafe {\r\n    font-size: 13px;\r\n  }\r\n}\r\n\r\n@media screen and (min-width: 650px) {\r\n  .app-bar {\r\n    grid-template-columns: 1fr auto;\r\n    padding: 8px 32px;\r\n  }\r\n\r\n  .app-bar .bar_brand h1 {\r\n    font-size: 1.5em;\r\n  }\r\n\r\n  .app-bar .bar_menu {\r\n    display: none;\r\n  }\r\n\r\n  .app-bar .bar_navigation {\r\n    position: static;\r\n    width: 100%;\r\n  }\r\n\r\n  .app-bar .bar_navigation ul li {\r\n    display: inline-block;\r\n  }\r\n\r\n  .app-bar .bar_navigation ul li a {\r\n    display: inline-block;\r\n    width: 120px;\r\n    text-align: center;\r\n    margin: 0;\r\n  }\r\n\r\n  .cafes {\r\n    grid-template-columns